<template>
  <div>
    <nav-head title="账号绑定"/>
    <div class="item-wrap">
      <div @click="phoneBind" class="item">
        <span>手机号</span>
        <span>{{BingUserInfo.mobile | phoneNumFormat}}</span>
        <span class="arrow-right"></span>
      </div>
      <!--<div @click="weixinBind" class="item">-->
        <!--<span>微信绑定</span>-->
        <!--<span>{{BingUserInfo.wxstatus === 1 ? '已绑定' : '未绑定'}}</span>-->
        <!--<span class="arrow-right"></span>-->
      <!--</div>-->
    </div>
    <v-dialog v-show="this.display" @close="onCloseDialog">
      <div class="text">是否确认解绑？</div>
      <div class="btn-group">
        <mt-button @click.native="onCloseDialog" class="btn-cancel">取消</mt-button>
        <mt-button @click.native="doUnbind" class="btn-confirm">确定</mt-button>
      </div>
    </v-dialog>
  </div>
</template>
<script>//
import NavHead from '@/components/NavHead.vue'
import VDialog from '../components/VDialog.vue'
import { Button, Toast } from 'mint-ui'
import { mapActions, mapState } from 'vuex'
import { GET_BIND_USER_INFO, UNBIND_WECHAT } from '../../../../vuex/Mine'

export default {
  name: 'accountBind',
  data () {
    return {
      display: false,
      unbindStatue: ''
    }
  },
  methods: {
    onCloseDialog () {
      this.display = false
    },
    doUnbind () {
      if (this.unbindStatue === 0) {
        this.unbindWechat({
          func: () => {
            this.display = false
            Toast({
              message: '微信解绑成功！',
              duration: 1000
            })
          }
        })
      }
    },
    phoneBind () {
      if (this.BingUserInfo.mobile) {
        this.$router.push({
          name: 'modifyBindPhone',
          params: { phoneNum: this.BingUserInfo.mobile }
        })
      } else {
        this.$router.push({
          name: 'bindPhone'
        })
      }
    },
    weixinBind () {
      if (this.BingUserInfo.wxstatus === 1) {
        this.display = true
        this.unbindStatue = 0
      } else {
        console.log('绑定微信账号！')
      }
    },
    ...mapActions({
      getBindUserInfo: GET_BIND_USER_INFO,
      unbindWechat: UNBIND_WECHAT
    })
  },
  components: {
    NavHead,
    VDialog,
    [Button.name]: Button
  },
  computed: {
    ...mapState({
      BingUserInfo: state => state.mine.BingUserInfo
    })
  },
  created () {
    this.getBindUserInfo()
  }
}
</script>
<style scoped lang="scss">
  div.item-wrap {
    background-color: $background-light-color;
    padding: 0 15px;
    margin-top: 15px;
    > div.item {
      height: 54px;
      line-height: 54px;
      text-align: left;
      font-size: 16px;
      color: $font-color;
      font-weight: bold;
      position: relative;
      border-bottom: 1px solid $border-color;
      span:nth-child(1) {
        margin-left: 10px;
      }
      span:nth-child(2) {
        float: right;
        margin-right: 25px;
        @if $IF365 { color: #CCC; }
        @else { color: #999; }
        font-weight: normal;
      }
      > span.arrow-right {
        position: absolute;
        right: 0;
        top: 15px;
      }
    }
    > div.item:last-child {
      border-bottom: none;
    }
  }
  .text {
    font-size: 16px;
    color: #333;
    font-weight: bold;
    line-height: 26px;
    margin: 15px 0 25px 0;
  }
  .btn-cancel{
    background: #DDD ;
    width: 40% ;
    height: 40px;
    color: #999;
    margin: 0 15px 0 0 ;
    font-size: 16px;
  }
  .btn-confirm{
    background: $back-color;
    width: 40% ;
    height: 40px;
    @if $IF365 { color: #131313; }
    @else { color: white; }
    font-size: 16px;
  }
</style>
